
<div class="filter-bar">
  <div class="ui form">
    <div class="inline field">
      <label class="text-gray-600">Search for:</label>
      <input
        v-model="filterText"
        type="text"
        class="text nicetext mx-2"
        placeholder=""
        @keyup="doFilter"
      >
      <button
        class="btn delete icon"
        @click="resetFilter"
      >
        Reset
      </button>
    </div>
  </div>
</div>
